@import url(https://fonts.googleapis.com/css?family=Lobster|Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* */
body{
font-family:arial;
background-color:cornflowerblue;
}
*{
box-sizing:border-box;
}
#box{
max-width:700px;
margin:auto;
margin-top:200px;
}

input[type=text]{
padding:17px;
font-size:20px;
border:none;
float:left;
width:85%;
background:linear-gradient(#ffffff,#3399ccc);
background:-webkit-linear-gradient(#ffffff,#3399ccc);
border-radius:5px 0 0 5px;
}


button{
float:left;
width:15%;
padding:17px;
background:linear-gradient(#3366ff,#336699);
background:-webkit-linear-gradient(#3366ff,#336699);
color:white;
font-size:20px;
border:none;
cursor:pointer;
border-radius:0 5px 5px 0;

}


